import React, { useCallback, useEffect, useRef, useState } from 'react'
import { Editor, EditorState, CompositeDecorator, SelectionState, Modifier, ContentState, RichUtils, getDefaultKeyBinding } from "draft-js";
import { Textarea } from '../components/ui/textarea'
import { Slider } from '../components/ui/slider'
import { Button } from '../components/ui/button'
import NavBar from '../components/navbar'
import { Select, SelectContent, SelectGroup, SelectItem, SelectLabel, SelectSeparator, SelectTrigger, SelectValue } from '../components/ui/select'
import { Input } from '../components/ui/input'
import { Loader2, RectangleHorizontalIcon } from 'lucide-react'
import { Tooltip, TooltipContent, TooltipTrigger } from '../components/ui/tooltip'
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '../components/ui/dialog'
import { Label } from '../components/ui/label'
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '../components/ui/alert-dialog'
import { useMetaKeyPress } from '../lib/metakeypress'
import { useKeyPress } from '../lib/keypress'
import 'draft-js/dist/Draft.css';

// CONSTANTS
const ENDPOINT_URL = "http://0.0.0.0:5000"
const default_settings = {
  "example_elephant" : `{"model_name":"cohere:command-xlarge-nightly","temperature":0.9,"maximum_length":500,"top_p":0.75,"top_k":0,"repetition_penalty":1,"num_beams":1,"num_return_sequences":1,"prompt":"Write me a short story about a elephant stuck in space"}`,
  "example_eggtart" : `{"model_name":"cohere:command-xlarge-nightly","temperature":1.0,"maximum_length":668,"top_p":0.75,"top_k":0,"repetition_penalty":1,"num_beams":1,"num_return_sequences":1,"prompt":"Please give me a recipe for Portuguese egg tarts"}`,
}

const styleMap = {
  'HIGHLIGHT': {
    'backgroundColor': '#faed27',
  },
  'NORMAL': {
    'backgroundColor': 'transparent',
  },
  'BOLD': {
    'fontWeight': 'bold',
  },
};

const styles = {
	openai: {
		backgroundColor: '#b9eebc',
		padding: '2px 0'
	},
	textgeneration: {
		backgroundColor: '#f6b2b3',
		padding: '2px 0'
	},
	cohere: {
		backgroundColor: '#a198e6',
		padding: '2px 0'
	},
  default :{
  }
};

// model specific text highlighting 
function getDecoratedStyle(model_name : any) {
  const prefix = model_name.split(":")[0]
	switch (prefix) {
		case 'openai':
			return styles.openai;
		case 'textgeneration':
			return styles.textgeneration;
		case 'cohere':
			return styles.cohere;
		default:
			return styles.default;
	}
}



export default function Playground() {
  // PROBABLY USEUSE EFFECT TO CREATE A "HISTORY"
  // NEED TO ADD STOP SEQUENCES
  const [model, setModel] = React.useState<string>("")
  const [availableModels, setAvailableModels] = React.useState({})
  // MODEL PARAMETERS
  const [temperature, setTemperature] = React.useState<number>(0.5)
  const [maximumLength, setMaximumLength] = React.useState<number>(200)
  const [topP, setTopP] = React.useState<number>(0.75) // hugging face wants defualt of 1.0 and cohere is 0.75
  const [topK, setTopK] = React.useState<number>(50)
  const [repetitionPenalty, setRepetitionPenalty] = React.useState<number>(1.0)
  const [numBeams, setNumBeams] = React.useState<number>(1)
  const [numReturnSequences, setNumReturnSequences] = React.useState<number>(1)
  const [frequencyPenalty, setFrequencyPenalty] = React.useState<number>(0.0)
  const [presencePenalty, setPresencePenalty] = React.useState<number>(0.0)
  // TEXT AREA CONTROL
  const [prompt, setPrompt] = React.useState<string>("")
  const [output, setOutput] = React.useState<string>("") // use this potentially for model output and green text?
  const [prePrompt, setPrePrompt] = React.useState<string>("")
  const scrollRef = useRef(null); // create a ref to the scroll parent element
  // LOADING STATE
  const [generating, setGenerating] = React.useState<boolean>(false)
  const [modelLoading, setModelLoading] = React.useState<boolean>(false)
  // PRESET CONTROL
  const [preset, setPreset] = React.useState<string | undefined>("")
  const [saveOpen, setSaveOpen] = React.useState<boolean>(false)
  const [presetName, setPresetName] = React.useState<string>("")
  const [presetList, setPresetList] = React.useState<string[]>([])
  // ABORT CONTROLLER FOR FETCH
  const abortController = useRef(null)

  // TEXT EDITOR FUNCTIONS
  // TEXT EDITOR DECORATOR HELPER
  const Decorated = (props : any) => {
    const children = props.children
    const contentState = props.contentState
    const entity =  props.contentState.getEntity(props.entityKey)
    const style = getDecoratedStyle(entity.getData().model);
    if (entity.getData().output === props.decoratedText) {
      return <span style={style} data-offset-key={children[0].key}>{children}</span>;
    } else {
      return <span data-offset-key={children[0].key}>{children}</span>;
    }
  };

  // TEXT EDITOR ENTITY BASED STRATEGY
  function findEntityRangesByType(entityType: any) {
    return (contentBlock: any, callback: any, contentState: any) => {
      contentBlock.findEntityRanges((character : any) => {
          const entityKey = character.getEntity()
          if (entityKey === null) {
            return false;
          }
          return contentState.getEntity(entityKey).getType() === entityType;
        },
        callback
      );
    };
  }

  const getEditorState = useCallback((): EditorState => {
      return editorStateRef.current
  }, [])

  const createDecorator = () =>
    new CompositeDecorator([
      {
        strategy: findEntityRangesByType('HIGHLIGHTED_WORD'),
        component: Decorated,
        props : {
          getEditorState,
        },
      }
  ]);

  // EDITOR STATE
  const [editorState, setEditorState] = React.useState(
    EditorState.createEmpty(createDecorator())
  );

  const editorStateRef = useRef<EditorState>(editorState)

  // PRESET LOADING ON PAGE LOAD
  useEffect(() => {
    // Get models to load
    let model_keys = Object.keys(localStorage).filter((key_name) => {
      if (key_name.startsWith("model_")) {
        return true
      }
      return false
    }).map(function(item, i) {
      return item.replace("model_", "")
    })

    var model_dict = {}
    model_keys.map((modelName) => {
      let modelProvider = localStorage.getItem("model_" + modelName) || "{}"
      if (modelProvider === "HuggingFace") {
        modelProvider = "textgeneration"
      } else if (modelProvider === "co:here") {
        modelProvider = "cohere"
      } else if (modelProvider === "OpenAI") {
        modelProvider = "openai"
      }
      model_dict[`${modelProvider}:${modelName}`] = modelName
    })

    console.log("model_dict on load", model_dict)
    setAvailableModels(model_dict)

    // Perform localStorage action, some sort of validation to only get the keys that are presets
    if (presetList.length == 0) {
      let keys = Object.keys(localStorage).filter((key_name) => {
        if (key_name.startsWith("preset_")) {
          return true
        }
        return false
      })
      keys.forEach(function(item, i) {
        keys[i] = item.replace("preset_", "")
      })
      console.log("KEYS", keys)
      setPresetList([...presetList, ...keys])
    }
  }, [])

  // HANDLE STREAMING CHARACTERS
  const handleStream = (e: MessageEvent) => {
    let resp = JSON.parse(e.data)
    console.log("STREAMING" + resp["message"])
    if (resp["message"] === "You have successfully connected.") {
      return
    } 
    setOutput(resp["message"]) // so useeffect can be called and update editor state
    // setPrompt(prompt => prompt + resp["message"])
  }

  // EDITOR UPDATER
  useEffect(() => {
    // add to words array
    console.log("OUTPUT:", output)
    const currentContent = editorState.getCurrentContent();
    // create new selection state where focus is at the end
    const blockMap = currentContent.getBlockMap();
    const key = blockMap.last().getKey();
    const length = blockMap.last().getLength();
    const selection = new SelectionState({
        anchorKey: key,
        anchorOffset: length,
        focusKey: key,
        focusOffset: length,
    });
    // Returns ContentState record updated to include the newly created DraftEntity record in it's EntityMap.
    let newContentState = currentContent.createEntity("HIGHLIGHTED_WORD", "MUTABLE", {model: model, output: output});
    // Call getLastCreatedEntityKey to get the key of the newly created DraftEntity record.
    const entityKey = currentContent.getLastCreatedEntityKey();
    //insert text at the selection created above 
    const textWithInsert = Modifier.insertText(currentContent, selection, output, null, entityKey);
    const editorWithInsert = EditorState.push(editorState, textWithInsert, 'insert-characters');
    //also focuses cursor at the end of the editor 
    const newEditorState = EditorState.moveSelectionToEnd(editorWithInsert);
    const finalEditorState = EditorState.forceSelection(newEditorState, newEditorState.getSelection())
    setEditorState(finalEditorState);
    // this is hacky to scroll to bottom
    if (scrollRef.current) {
      const scrollEl = scrollRef.current;
      scrollEl.scrollTop = scrollEl.scrollHeight - scrollEl.clientHeight;
    }
  }, [output])

  // Open up a stream and then hit stream inference endpoint
  const handleStreamingSubmit = async (regenerate=false, passedInPrompt="") => {
    // we can open the stream here as well and then terminate it
    // set up model
    if (model.startsWith("textgeneration:")) {
      setModelLoading(true)
      const model_set = await fetch(ENDPOINT_URL.concat("/api/setup-model"), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "model_name": model,
        })
      })
      if (!model_set.ok) {
        console.log(`Error: model not setup!`);
        // Stop loading
        setModelLoading(false);
        return;
      } else {
        console.log("Model setup!")
      }
      setModelLoading(false)
    }
    setGenerating(true)
    if (regenerate) {
      setPrePrompt(passedInPrompt)
    } else {
      setPrePrompt(prompt)
    }
    const sse = new EventSource(ENDPOINT_URL.concat("/api/listen"))
    sse.onmessage = e => {handleStream(e)} // update the prompt state
    // set proper endpoint for streaming
    let fetchEndpoint = ""
    if (model?.startsWith("openai:")) {
      fetchEndpoint = "/api/openai"
    } else if (model?.startsWith("textgeneration:")) {
      fetchEndpoint = "/api/inference/stream"
    }
    console.log("calling fetch")
    console.log(passedInPrompt) 
    // begin model loading and inference
    abortController.current = new AbortController();
    const res = await fetch(ENDPOINT_URL.concat(fetchEndpoint), {
      signal: abortController.current.signal,
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        model_name: model,
        temperature: temperature,
        maximum_length: maximumLength,
        top_p: topP,
        top_k: topK,
        repetition_penalty: repetitionPenalty,
        num_beams: numBeams,
        num_return_sequences: numReturnSequences,
        prompt: regenerate ? passedInPrompt : prompt
    })
    }).catch(e => {
      if (e.name === 'AbortError') {
        console.log("ABORTED")
      }
    })
    // close everything
    sse.close()
    setGenerating(false)
  }
  
  // Dispatch the fetch request to appropriate endpoint and connection type
  const handleSubmit = async (regenerate=false, passedInPrompt="") => {
    // TODO: change this to be cleaner
    if (model?.startsWith("textgeneration:") || model?.startsWith("openai:")) {
      return handleStreamingSubmit(regenerate, passedInPrompt)
    } 
    console.log("HANDLE SUBMIT FIRED")
    setGenerating(true)
    let fetchEndpoint = ""
    if (model?.startsWith("cohere:")) {
      fetchEndpoint = "/api/cohere"
    } else {
      fetchEndpoint = "/api/inference"
    }
    abortController.current = new AbortController();
    const res = await fetch(ENDPOINT_URL.concat(fetchEndpoint), {
      signal: abortController.current.signal,
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        model_name: model,
        temperature: temperature,
        maximum_length: maximumLength,
        top_p: topP,
        top_k: topK,
        presence_penalty: presencePenalty,
        frequency_penalty: frequencyPenalty,
        prompt: regenerate ? passedInPrompt : prompt
      })
    }).catch(e => {
      if (e.name === 'AbortError') {
        console.log("ABORTED")
        setGenerating(false);
        return
      } else {
        console.log("ERROR", e)
      }
    })
    // TODO: verbose clean up
    if (res) {
      const data = await res.json()
      if (!res.ok) {
        console.log(`Error: ${data.error}`);
        // Stop loading
        setGenerating(false);
        return;
      }
      console.log(data)
      if (regenerate) {
        // preprompt hasnt changed here
        setPrePrompt(passedInPrompt)
        setOutput(data)
      } else {
        setPrePrompt(prompt) // for undo last, just save the prompt before appending to it
        setOutput(data)
      }
    }
    setGenerating(false)
  }

  // save preset
  const handlePresetSave = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setSaveOpen(false)
    const settings = JSON.stringify({
        model_name: model,
        temperature: temperature,
        maximum_length: maximumLength,
        top_p: topP,
        top_k: topK,
        repetition_penalty: repetitionPenalty,
        num_beams: numBeams,
        num_return_sequences: numReturnSequences,
        prompt: prompt
    })
    savePreset(presetName, settings)
    setPresetList([...presetList, presetName])
    setPresetName("")
  }

  // when preset selected, load it into state
  const handlePresetSelect = (value: string) => {
    const settings = JSON.parse(getPreset(value) || "{}")
    // load it into current state
    setModel(settings.model_name)
    setTemperature(settings.temperature)
    setMaximumLength(settings.maximum_length)
    setTopP(settings.top_p)
    setTopK(settings.top_k)
    setRepetitionPenalty(settings.repetition_penalty)
    setNumBeams(settings.num_beams)
    setNumReturnSequences(settings.num_return_sequences)
    setPrompt(settings.prompt)
    setEditorState(EditorState.createWithContent(ContentState.createFromText(settings.prompt), createDecorator()))
    setPrePrompt("")
  }

  // handle preset reset to base state
  const resetToDefaultPreset = () => {
    setPreset("")
    setModel("")
    setTemperature(0.5)
    setMaximumLength(200)
    setTopP(1.0)
    setTopK(50)
    setRepetitionPenalty(1.0)
    setNumBeams(1)
    setNumReturnSequences(1)
    // the state stays the same here between prompt and editor
    setPrompt("")
    setEditorState(EditorState.createWithContent(ContentState.createFromText(""), createDecorator()))
    setPrePrompt("")
  }

  // handle undo last, update editor state 
  const handleUndoLast = () => {
    // state stays same here between prompt and editor
    setEditorState(EditorState.createWithContent(ContentState.createFromText(prePrompt), createDecorator()))
    setPrompt(prePrompt)
    setPrePrompt("")
  }

  // HELPER FUNCTIONS
  const editorRef = React.useRef(null);
  
  function focusEditor() {
    editorRef.current.focus();
  }

  // abort the fetch if the user clicks the cancel button
  const abortFetch = () => abortController.current && abortController.current.abort("User requested abort")

  // Get preset from user selection, load it into state
  const getPreset = (name: string) => {
    let current_preset = ""
    if (name.startsWith("example_")) {
      current_preset = default_settings[name as keyof typeof default_settings]
    } else {
      current_preset = localStorage.getItem("preset_" + name)! // presets are prefixed with preset_ in localstorage, just for ease
    }
    console.log("CURRENT PRESET" + current_preset)
    return current_preset
  }

  // Save preset to local storage
  const savePreset = (name: string, data: any) => {
    localStorage.setItem("preset_" + name, data)
  }

  // KEYBOARD SHORTCUT HANDLERS

  // Meta Key + Enter - Submit form
  const onEnterKeyPress = (event: any) => {
    handleSubmit();
  };
  useMetaKeyPress(['Enter'], onEnterKeyPress);

  // Esc - Abort request
  const onEscKeyPress = (event: any) => {
    abortFetch();
  };
  useKeyPress(['Escape'], onEscKeyPress);

  // EDITOR KEY COMMAND HANDLER
  const handleKeyCommand = (command: any, editorState: any) => {
    if (command === 'bold') {
      setEditorState(RichUtils.toggleInlineStyle(editorState, 'BOLD'))
      return 'handled'
    }
    return 'not-handled'
  }

   // Define a custom keyBindingFn function that maps the Ctrl+B keyboard shortcut to a 'bold' command
   const keyBindingFn = (event: any) => {
    if (event.metaKey && event.keyCode === 66) {
        // Meta Key+B
        return 'bold'
    } else if (event.ctrlKey && event.keyCode === 66) {
        return 'bold'
    }
    return getDefaultKeyBinding(event)
  }

  return (
    <>
        <NavBar tab="playground">
            <div className='float-right'>
              <Dialog open={saveOpen} onOpenChange={setSaveOpen}>
                <DialogTrigger asChild>
                  <Button variant="subtle">Save</Button>
                </DialogTrigger>
                <DialogContent className="sm:max-w-[425px]">
                  <DialogHeader>
                    <DialogTitle>Save Preset</DialogTitle>
                    <DialogDescription>
                      This will save the current playground state as a preset which you can access it later.
                    </DialogDescription>
                  </DialogHeader>
                  <form onSubmit={handlePresetSave}>
                    <div className="grid gap-4 py-2">
                      <div className="grid w-full max-w-sm items-center">
                        <Label htmlFor="name">Name</Label>
                        <Input className="mt-2 text-left" type="text" id="name" placeholder="Name" onChange={(e) => setPresetName(e.target.value)} required/>
                      </div>
                      <div className="grid w-full max-w-sm items-center gap-1.5">
                        <Label htmlFor="desc">Description (Optional)</Label>
                        <Input className="mt-2 text-left" type="text" id="desc" placeholder="A preset for..." />
                      </div>
                    </div>
                    <DialogFooter>
                      {/* <Button variant="subtle" type="reset">Cancel</Button> */}
                      <Button type="submit" value="submit">Save</Button>
                    </DialogFooter>
                  </form>
                </DialogContent>
              </Dialog>
            </div>
            { preset != "" ? 
              <div className='float-right'>
                <Button variant="subtle" onClick={resetToDefaultPreset}>Reset Preset</Button>
              </div>
            : null}
            <div className='float-right w-64'>
              <Select value={preset} onValueChange={(value) => {setPreset(value); handlePresetSelect(value)}}>
                    <SelectTrigger className="w-full">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                    <SelectGroup>
                      <SelectLabel>My Presets</SelectLabel>
                      {presetList.map((preset, i) => (
                        <SelectItem key={i} value={preset}>{preset}</SelectItem>
                      ))}
                      <SelectSeparator />
                    </SelectGroup>
                    <SelectGroup>
                      <SelectLabel>Examples</SelectLabel>
                      <SelectItem value="example_elephant">Elephant Story</SelectItem>
                      <SelectItem value="example_eggtart">Egg Tart</SelectItem>
                      <SelectSeparator />
                    </SelectGroup>
                    </SelectContent>
                </Select>
            </div>
        </NavBar>
        <div className='flex flex-col font-display'>
          { /* TEXTAREA COMPONENT */ }
          <form onSubmit={(e) => {e.preventDefault(); handleSubmit()}}>
          <div className='flex flex-row space-x-4 flex-grow ml-5 mr-5'>
              <div className="h-[80vh] flex-grow basis-5/6">
                <div ref={scrollRef} onClick={focusEditor} className="overflow-y-auto editor-container h-full w-full py-3 px-3 text-base rounded-md border border-slate-300">
                  <Editor
                    ref={editorRef}
                    keyBindingFn={keyBindingFn}
                    handleKeyCommand={handleKeyCommand}
                    customStyleMap={styleMap}
                    editorState={editorState}
                    onChange={(editorState: any) => {setEditorState(editorState); setPrompt(editorState.getCurrentContent().getPlainText('\u0001'))}}
                  />
                </div>
                {/* <Textarea className='h-full w-full' placeholder="Write tagline for a ice cream shop." value={prompt} onChange={(e) => setPrompt(e.target.value)} required/> */}
                <div className='space-x-4'>
                  {modelLoading && 
                    <Button className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" disabled>
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                      Loading Model
                    </Button>
                  }
                  {generating && 
                    <Button type="button" variant="subtle" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" onClick={e => {e.stopPropagation(); abortFetch()}}> <Loader2 className="mr-2 h-4 w-4 animate-spin" />Cancel Generation</Button>
                  }
                  {(!modelLoading && !generating) &&
                    <Button variant="default" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" type="submit" value="submit">Submit</Button> 
                  }
                  <Button type="button" variant="subtle" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" onClick={handleUndoLast} disabled={prePrompt === ""}>Undo Last</Button> 
                  <Button type="button" variant="subtle" className="inline-flex items-center px-5 py-2.5 mt-4 text-sm font-medium text-center" onClick={e=> {e.stopPropagation(); handleUndoLast(); handleSubmit(true, prePrompt)}} disabled={prePrompt === ""}>Regenerate Output</Button> 
                </div>
              </div>
            { /* SIDE BAR CHANGING VALUES */}
            <div className="grid grid-rows-8 basis-1/6 overflow-auto">
              <div className=''>
                <span className='flow-root inline-block align-middle mb-2'>
                    <p className="text-sm font-medium float-left align-text-top">
                      Model
                    </p>  
                </span>
                <Select value={model} onValueChange={setModel} required={model === ""}>
                  <SelectTrigger className="w-full">
                    <SelectValue placeholder="Select a Model" />
                  </SelectTrigger>
                  <SelectContent onKeyDown={(e) => { if (e.code === "Enter") { e.stopPropagation(); } }}>
                    <SelectGroup>
                      <SelectLabel>Available Models</SelectLabel>
                      {Object.entries(availableModels).map(([model_key, model_name]) => (
                        <SelectItem key={model_key} value={model_key}>{model_name}</SelectItem>
                      ))}
                    </SelectGroup>
                  </SelectContent>
                </Select>
              </div>
              { /* TEMPERATURE */ }
              { model?.startsWith("cohere:") ? 
               <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                  <span className='flow-root inline-block align-middle mb-3'>
                    <p className="text-sm font-medium float-left align-text-top">
                      Temperature
                    </p>  
                    <Input className='float-right text-sm' type="number" value={[temperature.toString()]} step={0.01} min={0} max={5} onChange={e => {let val = parseFloat(e.target.value); if (val > 1) { val = 1 } else if (val < 0) { val = 0 } setTemperature(val)}} />
                  </span>
                  <Slider key="temperature" defaultValue={[temperature]} min={0} max={5} step={0.01} onValueChange={e => setTemperature(e[0])} onValueCommit={e => setTemperature(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>A non-negative float that tunes the degree <br/> of randomness in generation. Lower temperatures <br/> mean less random generations. <b> Defaults to 0.5, min of 0.0 and max to 5.0</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                  <span className='flow-root inline-block align-middle mb-3'>
                    <p className="text-sm font-medium float-left align-text-top">
                      Temperature
                    </p>  
                    <Input className='float-right text-sm' type="number" value={[temperature.toString()]} step={0.01} min={0} max={1} onChange={e => {let val = parseFloat(e.target.value); if (val > 1) { val = 1 } else if (val < 0) { val = 0 } setTemperature(val)}} />
                  </span>
                  <Slider key="temperature" defaultValue={[temperature]} min={0} max={1} step={0.01} onValueChange={e => setTemperature(e[0])} onValueCommit={e => setTemperature(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>A non-negative float that tunes the degree <br/> of randomness in generation. Lower temperatures <br/> mean less random generations. <b> Defaults to 0.5</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              }
              { /* MAX LENGTH */ }
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Maximum length
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[maximumLength.toString()]} step={1} onChange={e => setMaximumLength(parseInt(e.target.value))} min={50} max={1024} />
                    </span>
                    <Slider defaultValue={[maximumLength]} value={[maximumLength]} min={50} max={1024} step={1} onValueChange={e => setMaximumLength(e[0])} onValueCommit={e => setMaximumLength(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>Maximum number of tokens to generate. <br/> Responses are not guaranted to fill up <br /> to the maximum desired length. <br/>  <b>Defaults to 200.</b> </p>
                </TooltipContent>
                </Tooltip>
              </div>
              { /* TOP P */ }
              { model?.startsWith("cohere:") ? 
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Top P
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[topP.toString()]} min={0} max={1} step={0.01} onChange={e => setTopP(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[topP]} value={[topP]} min={0} max={1} step={0.01} onValueChange={e => setTopP(e[0])} onValueCommit={e => setTopP(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>If set to float less than 1, only the smallest <br/> set of most probable tokens with probabilities <br/> that add up to top_p or higher are kept for generation. <br /> <b>Defaults to 0.75</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Top P
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[topP.toString()]} min={0} max={2} step={0.01} onChange={e => setTopP(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[topP]} value={[topP]} min={0} max={2} step={0.01} onValueChange={e => setTopP(e[0])} onValueCommit={e => setTopP(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>If set to float less than 1, only the smallest <br/> set of most probable tokens with probabilities <br/> that add up to top_p or higher are kept for generation. <br /> <b>Defaults to 1.0</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
      }
              { /* TOP K */ }
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Top K
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[topK.toString()]} min={0} max={500} step={1} onChange={e => setTopK(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[topK]} value={[topK]} min={0} max={500} step={1} onValueChange={e => setTopK(e[0])} onValueCommit={e => setTopK(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>The number of highest probability vocabulary <br/> tokens to keep for top-k-filtering. <b>Defaults to 50.</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              { /* FREQUENCY PENALTY */ }
              { model?.startsWith("cohere:") ? 
                <div className=''>
                  <Tooltip delayDuration={300} skipDelayDuration={150}>
                  <TooltipTrigger asChild>
                    <div>
                      <span className='flow-root inline-block align-middle mb-3'>
                        <p className="text-sm font-medium float-left align-text-top">
                          Frequency Penalty
                        </p>  
                        <Input className='float-right text-sm' type="number" value={[frequencyPenalty.toString()]} min={0} max={1} step={0.01} onChange={e => setFrequencyPenalty(parseInt(e.target.value))}/>
                      </span>
                      <Slider defaultValue={[frequencyPenalty]} value={[frequencyPenalty]} min={0} max={1} step={0.01} onValueChange={e => setFrequencyPenalty(e[0])} onValueCommit={e => setFrequencyPenalty(e[0])}/>
                    </div>
                  </TooltipTrigger>
                  <TooltipContent side='left'>
                    <p> Can be used to reduce repetitiveness of generated tokens. The higher  <br/> the value, the stronger a penalty is applied to previously present tokens, <br/> proportional to how many times they have already appeared  <br/> in the prompt or prior generation. <b>Defaults to 0.0, max value of 1.0</b></p>
                  </TooltipContent>
                  </Tooltip>
                </div>
              : null}
              { /* PRESENCE PENALTY */ }
              { model?.startsWith("cohere:") ? 
                <div className=''>
                  <Tooltip delayDuration={300} skipDelayDuration={150}>
                  <TooltipTrigger asChild>
                    <div>
                      <span className='flow-root inline-block align-middle mb-3'>
                        <p className="text-sm font-medium float-left align-text-top">
                          Presence Penalty
                        </p>  
                        <Input className='float-right text-sm' type="number" value={[presencePenalty.toString()]} min={0} max={1} step={0.01} onChange={e => setPresencePenalty(parseInt(e.target.value))}/>
                      </span>
                      <Slider defaultValue={[presencePenalty]} value={[presencePenalty]} min={0} max={1} step={0.01} onValueChange={e => setPresencePenalty(e[0])} onValueCommit={e => setPresencePenalty(e[0])}/>
                    </div>
                  </TooltipTrigger>
                  <TooltipContent side='left'>
                    <p> Can be used to reduce repetitiveness of generated tokens. Similar to <br/> frequency_penalty, except that this penalty is applied equally <br/> to all tokens that have already appeared, regardless of their <br/> exact frequencies. <b>Defaults to 0.0, max value of 1.0</b></p>
                  </TooltipContent>
                  </Tooltip>
                </div>
              : null}
              { /* Repetition penalty */ }
              { model?.startsWith("cohere:") ? null :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Repetition Penalty
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[repetitionPenalty.toString()]} min={0} max={2} step={0.01} onChange={e => setRepetitionPenalty(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[repetitionPenalty]} value={[repetitionPenalty]} min={0} max={2} step={0.01} onValueChange={e => setRepetitionPenalty(e[0])} onValueCommit={e => setRepetitionPenalty(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>Akin to presence penalty. The repetition penalty is meant <br/>  to avoid sentences that repeat themselves without <br/> anything really interesting. <b>Defaults to 1.0, means no penalty.</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
      }
              { /* Num Beams */ }
              { model?.startsWith("cohere:") ? null :
              <div className=''>
                <Tooltip delayDuration={300} skipDelayDuration={150}>
                <TooltipTrigger asChild>
                  <div>
                    <span className='flow-root inline-block align-middle mb-3'>
                      <p className="text-sm font-medium float-left align-text-top">
                        Number of Beams
                      </p>  
                      <Input className='float-right text-sm' type="number" value={[numBeams.toString()]} min={0} max={10} step={1} onChange={e => setNumBeams(parseInt(e.target.value))}/>
                    </span>
                    <Slider defaultValue={[numBeams]} value={[numBeams]} min={1} max={10} step={1} onValueChange={e => setNumBeams(e[0])} onValueCommit={e => setNumBeams(e[0])}/>
                  </div>
                </TooltipTrigger>
                <TooltipContent side='left'>
                  <p>Number of beams for beam search. <br/> <b>1 means no beam search.</b></p>
                </TooltipContent>
                </Tooltip>
              </div>
              }
            </div>
            </div>
        </form>
      </div>
    </>
  )
}
